import React from 'react';
import { useNavigate } from 'react-router-dom';

const BalanceWrapper = () => {
  const navigate = useNavigate()
  return (
  <>
  <div className="announcement">
    <div>
      <img src={require("../images/announcement.png")} alt="" />
      Announcement :
    </div>
    <div className="running_text">
       <marquee scrolldelay="200" data-dom="announcement" class="dom-registered">
            Welcome to xxxxx! We are implementing Help2pay &amp; Eeziepay system, now you can enjoy faster processing time when deposit!&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            </marquee>
    </div>
  </div>
  

    <div className="balance_wrapper">
        <div className="links">
          <a href=".#" onClick={e => {
            e.preventDefault()
            navigate("/")
          }}>
            <img src={require("../images/btn-login.png")} alt=""  className='img-fluid'/>
            <span>Login</span></a>
          <a href=".#" className='register' onClick={e => {
            e.preventDefault()
            navigate("/signup")
          }}>
            <img src={require("../images/btn-register.png")} alt=""  className='img-fluid'/>
            <span>Register</span></a>
        </div>
      <div className="pbalance">
        <div className="balance_btns">
          <a href=".#" className="btns" onClick={e => {
            e.preventDefault()
            navigate("/deposit")
          }}><span className="main_rounded">
          <img src={require("../../src/images/deposit.png")} alt="" width="25px" height="25px"/></span>Deposit</a>

          <a href=".#" className="btns withdraw_btn" onClick={e => {
            e.preventDefault()
            navigate("/")
          }}><span className="main_rounded">
          <img src={require("../../src/images/transfer.png")} alt="" width="25px" height="25px"/></span>Transfer</a>

          <a href=".#" className="btns refresh_btn" onClick={e => {
            e.preventDefault()
            navigate("/withdraw")
          }} ><span className="main_rounded">
          <img src={require("../../src/images/withdraw.png")} alt="" width="25px" height="25px"/></span>Withdraw</a>

          <a href=".#" className="btns refresh_btn" onClick={e => {
            e.preventDefault()
            navigate("/home")
          }} ><span className="main_rounded">
          <img src={require("../../src/images/notification.png")} alt="" width="25px" height="25px"/></span>Notification</a>
        </div>
      </div>
    </div>
     </>
  )
}

export default BalanceWrapper